.page {
	display: grid;
	grid-template-columns: var(--space-md) 1fr var(--space-md);
	> * {
		min-width: 0;
		grid-column: 2 / 3;
		&.alignwide {
			grid-column: 2 / 3;
		}
		&.alignfull {
			grid-column: 1 / 4;
		}
	}
	@include sm-and-up {
		grid-template-columns: 8vw 1fr minmax(20px, #{$breakpoint-sm}) 1fr 8vw;
		> * {
			grid-column: 3;
			&.alignwide {
				grid-column: 2 / 5;
			}
			&.alignfull {
				grid-column: 1 / 6;
			}
		}
	}
	> * {
		margin: var(--space-lg) auto;
	}
}

.aligncenter {
	width: 100%;
	max-width: #{$breakpoint-sm};
	margin: var(--space-lg) auto;
}

.alignwide {
	width: 100%;
	margin: var(--space-lg) auto;
	@include md-and-up {
		max-width: #{$breakpoint-lg};
	}
}

.alignfull {
	max-width: none;
	width: 100%;
	margin: 0 auto;
}
